.App {
  font-family: "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro",
    "游ゴシック", "Yu Gothic", "Meiryo", sans-serif;
  padding: clamp(16px, 2vw, 32px);
  width: min(92vw,  100vw);
  min-height: 90vh;
  margin: 5vh auto;
  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* タイトルは中央 */
.App-title {
  text-align: center;
  font-size: clamp(28px, 2vw, 56px);
  margin-bottom: 3vh;
}

/* 本文は両端揃え */
.App-text {
  text-align: justify;
  line-height: 1.8;
  word-break: keep-all;
  white-space: pre-line;
}

/* ボタンを中央寄せ */
.App-button-wrapper {
  text-align: center;
  margin-top: clamp(16px, 4vh, 48px);
}

.App-button {
  padding: clamp(10px, 1.5vh, 16px) clamp(16px, 2vw, 24px);
  font-size: clamp(14px, 1.4vw, 18px);
  border-radius: 1vw;
  cursor: pointer;
}

@media (max-width: 480px) {
  .App {
    margin: 2vh auto;
    min-height: 96vh;
  }
  .App-button {
    width: 100%;
  }
}

/* ナビゲーション */
.nav-bar {
  display: flex;
  gap: 8px;
  padding: 12px;
  position: sticky;
  top: 0;
  background: var(--nav-bg, rgba(250,250,250,.75));
  backdrop-filter: saturate(1.2) blur(6px);
  z-index: 5;
  flex-wrap: wrap; /* ← スマホ幅で折り返し */
}

.nav-link {
  padding: 6px 10px;
  border-radius: 8px;
  text-decoration: none;
  color: inherit;
  border: 1px solid rgba(128,128,128,.2);
}

.nav-link.active {
  background: rgba(128,128,128,.15);
}

/* NotFound ページ */
.notfound {
  padding: 16px;
}

.notfound-title {
  margin-top: 0;
}

.notfound-link {
  color: blue;
}

/* ===== IncomeCalc layout ===== */
/* --- global sizing to avoid overflow --- */
*, *::before, *::after { box-sizing: border-box; }

.grid2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(12px, 2.5vw, 24px);
  align-items: start;
}
@media (max-width: 900px) {
  .grid2 { grid-template-columns: 1fr; }
}

.card {
  background: color-mix(in oklab, canvas 96%, transparent);
  border: 1px solid color-mix(in oklab, canvastext 14%, transparent);
  border-radius: 14px;
  padding: clamp(12px, 2vw, 24px);
}

.row {
  display: grid;
  grid-template-columns: 1fr 1.6fr; /* 入力側を広めに */
  gap: clamp(8px, 2vw, 16px);
  align-items: center;
  margin: 0.6em 0;
}
.row > label {
  opacity: .85;
  padding-inline-end: 0.3em;
}

/* inputs: allow shrinking inside grid cell, keep within cell width */
.row input, .row select {
  width: 100%;
  max-width: 100%;
  min-width: 0;           /* Grid/Flex のデフォ最小幅(内容幅)を打ち消す */
  padding: 0.55em 0.7em;  /* 相対単位で余白 */
  border-radius: 0.6em;
  border: 1px solid color-mix(in oklab, canvastext 18%, transparent);
  background: canvas;
  color: canvastext;
}

/* focus 視認性（任意だが編集時に便利） */
.row input:focus, .row select:focus {
  outline: 2px solid color-mix(in oklab, #2684ff 60%, transparent);
  outline-offset: 2px;
}

/* table look */
.result-table th, .result-table td {
  padding: 8px;
  border-bottom: 1px dashed color-mix(in oklab, canvastext 12%, transparent);
}
.result-table tfoot td { font-weight: 700; }
.muted { opacity: .75; font-size: .9em; }

/* ===== Enhancements for IncomeCalc mobile overflow & tables ===== */

/* page wrapper (used by IncomeCalc) */
.page { padding: 12px; }

/* Horizontal scroll wrapper for wide tables (children list) */
.scroll-x {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

/* Generic table used in the children editor */
.table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;         /* Keep cells within available width */
  min-width: 560px;            /* Prevent cramped cells on mobile; adjust if needed */
}
.table th, .table td {
  padding: 8px;
  border-bottom: 1px solid color-mix(in oklab, canvastext 12%, transparent);
  vertical-align: middle;
  word-break: break-word;
}
.table th:nth-child(1) { width: 3.5rem; }  /* # */
.table th:nth-child(2) { width: 6rem; }    /* 年齢 */
.table th:nth-child(3) { width: 8rem; }    /* 年収 */
.table th:nth-child(4) { width: 8rem; }    /* 障害 */
.table th:nth-child(5) { width: 5rem; }    /* 同居 */

/* Inputs inside table cells should fit the cell */
.table input[type="number"],
.table select {
  width: 100%;
  min-width: 0;
  max-width: 100%;
  padding: 6px 8px;
  border-radius: 0.5em;
  border: 1px solid color-mix(in oklab, canvastext 18%, transparent);
  background: canvas;
  color: canvastext;
}

/* Utility layout helpers */
.flex { display: flex; gap: 8px; flex-wrap: wrap; }
.mt   { margin-top: 12px; }
.mb   { margin-bottom: 12px; }
.text-right { text-align: right; }

/* Badge-like statuses used in allowance results */
.ok {
  color: color-mix(in oklab, #0a7f26 85%, canvastext 15%);
  font-weight: 700;
}
.ng {
  color: color-mix(in oklab, #b00020 85%, canvastext 15%);
  font-weight: 700;
}

/* Divider */
.hr {
  height: 1px;
  background: color-mix(in oklab, canvastext 14%, transparent);
  margin: 12px 0;
}

/* Responsive tweaks for very small screens */
@media (max-width: 480px) {
  .grid2 { grid-template-columns: 1fr; }  /* already single column, ensure */
  .table { min-width: 420px; }            /* allow narrower table on small phones */
  .App-title { font-size: clamp(22px, 6vw, 28px); }
  .row { grid-template-columns: 1fr 1.2fr; }
  .row input, .row select { padding: 0.5em 0.6em; }
}